<template>

    <div id="orderList">
       
        <div class="shop"
             v-for="order in datasource">
            <div class="title">
                <h4>订单号：{{order.order_sn}}</h4> <span>{{order.status_name}}</span>
            </div>
            <router-link :to='{name:"orderdetail",params:{order_id:order.id}}' v-for=" good in order.has_many_order_goods">
                <div class="goods">
                    <div class="img"><img :src="good.thumb"></div>
                    <div class="inner">
                        <div class="name">{{good.title}}</div>
                        <div class="option">规格:  {{good.goods_option_title}}</div>
                    </div>
                    <div class="price">
                        ￥{{good.goods_price}}<br/>   ×{{good.total}}
                    </div>
                </div>

            </router-link>
            <div class="title" >
                <h4>共 {{order.goods_total}} 件商品 实付：￥<b>{{order.price}}</b></h4>
                <span class="del" v-if="order.button_models.length == 1" @click="operation(order.button_models[0],order)" >{{order.button_models[0].name}}</span>
            </div>
            <div class="allbt"
                 v-if="order.button_models.length > 1">
                <div style="flex: 3;"></div>
                    <span v-for="item in order.button_models"
                          @click="operation(item,order)">{{item.name}}</span>
            </div>
        </div>
    </div>
</template>
<script>
    import { Actionsheet } from 'mint-ui';
    import { MessageBox } from 'mint-ui';
    export default
    {

        //select_inde-当前选择行,datasource-数据源,currentdata-当前选择的数据
        props: ['datasource'],
        data() {
            return {
                isShow: false,
                //   actions:[],
                //   sheetVisible:''


            }
        },
        methods:
        {
            operation(btn, order) {

                //this.actions=[{name:"无理由",method:"sheetAction"},{name:"不退了",method:"sheetAction"}];
                if (btn.value == 8) {
                    this.checklogistics(order, btn);
                } else if (btn.value == 12) {
                    this.deleteOrder(order, btn);

                } else if (btn.value == 10) {
                    this.comment(order, btn);

                } else if (btn.value == 1) {
                    this.toPay(order, btn);
                } else if (btn.value == 13) {
                    //退款
                    this.toRefund(order, btn);

                } else if (btn.value == 9) {
                    //取消订单
                    this.cancleOrder(order, btn);

                } else if (btn.value == 5) {
                    //确认订单
                    this.confirmOrder(order, btn);

                }

            },
            toDetail(order) {

                this.$emit('ToDetailNotification', order);
            }
            ,
            //查看物流
            checklogistics(item, btn) {
                this.$router.push({ name: "logistics", params: { order: item } });

            },
            cancleOrder(item, btn) {
                MessageBox({
                    title: '提示',
                    message: '确定取消此订单?',
                    showCancelButton: true
                }).then(action => {
                    if (action == 'confirm') {
                    //删除当前选中的地址
                    this.datasource.splice(this.datasource.indexOf(item), 1);
                    this.sentRequest(btn.api, { order_id: item.id }, false);

                }

            });

            },
            //删除订单
            deleteOrder(item, btn) {
                MessageBox({
                    title: '提示',
                    message: '确定删除此订单?',
                    showCancelButton: true
                }).then(action => {
                    if (action == 'confirm') {
                    //删除当前选中的地址
                    this.datasource.splice(this.datasource.indexOf(item), 1);
                    this.sentRequest(btn.api, { order_id: item.id }, false);

                }

            });


            },
            toPay(item, btn) {

                this.$router.push({ name: "orderpay", params: { order_id: item.id } });

            },
            //评价订单
            comment(item, btn) {

                this.$router.push({ name: "comment", params: { order: item } });

            },
            confirmOrder(item, btn) {
                MessageBox({
                    title: '提示',
                    message: '您确定收到货物?',
                    showCancelButton: true
                }).then(action => {
                    if (action == 'confirm') {
                    this.$emit('ConfrimOrderNotification');
                    //调用接口通知服务器收到货物请求
                    // $http.get(btn.api,{order_id:item.id}).then(function (response)
                    //   {
                    //       this.$emit('ConfrimOrderNotification');

                    //   }, function (response) {
                    //                 // error callback
                    //               });

                } else {

                }

            });


            },
            //追加评价
            reCommend(item, btn) {

            },
            //退款
            toRefund(item, btn) {
                //refund
                this.$router.push({ name: "refund", params: { order_id: item.id ,type:1} });

            },
            sentRequest(action, params, isIndicator) {
                $http.get(action, params, '').then(function (response) {

                }, function (response) {
                    // error callback
                });
            },
            sheetAction() {
                console.log(this.sheetVisible);
            }

        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
    .shop {
        background: #FFF;
        margin-top: 2px;
        border-bottom: 1px solid #e2e2e2;
        border-top: 1px solid #e2e2e2;
    a {
        color: #000000;
    }
    .title {
        padding: 0 10px;
        display: flex;
        align-items: center;
    h4 {
        text-align: left;
        font-weight: normal;
        margin: 10px 0;
        flex: 8;
        font-size:.8rem;
    b{font-size: 1.1rem;}
    }
    span {
        color: #f15353;
        text-align: right;
        margin-right: 1px;
        flex: 2;
    }
    }
    span.del {
        border-radius: 14px;
        border: 1px solid #F15353;
        text-align: center;
        padding: 5px 7px;
        font-size: .7rem;
    }
    .allbt {
        display: flex;
        align-items: center;
        margin: 0 0 10px;
        -ms-flex-pack: justify;
        justify-content: space-between;
        border-top: 1px solid #e2e2e2;
        padding-top: 10px;
    span {
        flex: 2;
        border-radius: 14px;
        border: 1px solid #b1a6a6;
        margin: 0 10px 0 0;
        padding: 5px 7px;
        text-align: center;
        font-size: .7rem;
    }
    span:last-child {
        color: #f15353;
        border: 1px solid #f15353;
    }
    }
    }

    .goods {
        display: flex;
        align-items: stretch;
        flex-flow: row wrap;
        background: #f5f5f5;
        padding: 10px;
    .img {
        flex: 3;
    img {
        width: 100%;
    }
    }
    .inner {
        flex: 5;
        padding: 0 5px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .name {
        flex: 5;
        text-align: left
    }
    .price {
        flex: 2;
        align-items: center;
        text-align: end;
    }
    .option {
        color: #888;
        font-size: .6rem;
        flex: 1;
    }
    }

    #tbs {
        display: flex;
        align-items: center;
        background: #FFF;
        flex-flow: row wrap;
        padding: 10px;
        line-height: 1.5rem;
        border-bottom: 1px solid #D9D9D9;
    .left {
        text-align: left;
        flex: 50%;
        color: #858585;
    }
    .right {
        text-align: right;
        flex: 50%;
    }
    .add {
        color: #259b24;
    }
    .reduce {
        color: #e51c23;
    }
    }
</style>



