<template>
	<div id="payment"
	     v-infinite-scroll="loadBottom"
	     infinite-scroll-disabled="loading"
	     infinite-scroll-distance="10">
		<c-title :hide="false"
		         text='我的订单'></c-title>
		<div style="height: 87px;"></div>
		<mt-navbar v-model="selected"
		           fixed>
			<mt-tab-item id="0"
			             @click.native="swichTabTItem">全部</mt-tab-item>
			<mt-tab-item id="1"
			             @click.native="swichTabTItem"> 待付款</mt-tab-item>
			<mt-tab-item id="2"
			             @click.native="swichTabTItem">待发货</mt-tab-item>
			<mt-tab-item id="3"
			             @click.native="swichTabTItem">待收货</mt-tab-item>
			<mt-tab-item id="4"
			             @click.native="swichTabTItem">已完成</mt-tab-item>
		</mt-navbar>

		<mt-loadmore v-if="1"
		             :top-method="loadTop"
		             :bottom-method="loadBottom"
		             :bottom-all-loaded="allLoaded"
		             ref="loadmore"
		             bottomPullText=''
		             bottomDropText='下拉加载...'
		             bottomLoadingText=''
		             :autoFill='true'>
			<mt-tab-container v-model="selected">
				<mt-tab-container-item id="0">
					<cOrderList v-on:ConfrimOrderNotification="cofirmOrderAction"
					            v-on:ToDetailNotification="toDetail"
					            :datasource="orderList"></cOrderList>
				</mt-tab-container-item>
				<mt-tab-container-item id="1">
					<cOrderList v-on:ConfrimOrderNotification="cofirmOrderAction"
					            v-on:ToDetailNotification="toDetail"
					            :datasource="waitPayList"></cOrderList>
				</mt-tab-container-item>
				<mt-tab-container-item id="2">
					<cOrderList v-on:ConfrimOrderNotification="cofirmOrderAction"
					            v-on:ToDetailNotification="toDetail"
					            :datasource="waitSendList"></cOrderList>
				</mt-tab-container-item>
				<mt-tab-container-item id="3">
					<cOrderList v-on:ConfrimOrderNotification="cofirmOrderAction"
					            v-on:ToDetailNotification="toDetail"
					            :datasource="waitReceiveList"></cOrderList>
				</mt-tab-container-item>
				<mt-tab-container-item id="4">
					<cOrderList v-on:ConfrimOrderNotification="cofirmOrderAction"
					            v-on:ToDetailNotification="toDetail"
					            :datasource="waitCompleteList"></cOrderList>
				</mt-tab-container-item>
			</mt-tab-container>
		</mt-loadmore>
		<!--<div class="loadNomore"
		     v-show='loading'><img src="assets/images/no-more-product.png" /></div>-->
	</div>
</template>
<script>
import orderListController from './order_listController';
export default orderListController;

</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.loadNomore img {
	width: 20%;
}

#payment {
	.mint-navbar.is-fixed {
		top: 40px;
	}
}
</style>


